import { createI18n } from 'vue-i18n'
import en from '@/assets/lang/en.json'
import zh from '@/assets/lang/zh.json'

const i18n = createI18n<{
    legacy: boolean,
    locale: string,
    fallbackLocale: string,
    messages: { [key: string]: any }
}>({
    legacy: false,
    locale: 'zh',
    fallbackLocale: 'zh',
    messages: {
        'en': en,
        'zh': zh
    }
})

export default i18n