
import { defineComponent } from "vue";
import { useI18n } from "vue-i18n";
import { toggleDetailsModalObjType } from "./types";

interface Props {
  toggleDetailsModalObjProps: toggleDetailsModalObjType;
  toggleDetailsModalProps: boolean;
  closeDetailsModalProps: (status: boolean) => void;
}

interface methodType {
  formatLanguage: (formatStr: string) => string;
  useCloseDetailsModalProps: (status: boolean) => void;
}

export default defineComponent({
  props: [
    "toggleDetailsModalObjProps",
    "toggleDetailsModalProps",
    "closeDetailsModalProps",
  ],
  setup(props: Props) {
    const { t } = useI18n();

    const method: methodType = {
      formatLanguage(formatStr) {
        return t(formatStr);
      },
      useCloseDetailsModalProps(status) {
        const { closeDetailsModalProps }: Props = props;
        closeDetailsModalProps(status);
      },
    };
    return { ...method };
  },
});
