
import $ from "../lib/Library";
import {
  computed,
  defineComponent,
  onBeforeMount,
  onMounted,
  onUnmounted,
  onUpdated,
  ref,
  Ref,
} from "vue";
import { skillItem, collectionItem } from "../assets";
import Modal from "../components/Modal.vue";
import DetailsModal from "../components/DetailsModal.vue";
import {
  toggleModalObjType,
  skillItemType,
  collectionItemType,
  goToTitleNameType,
  languageGroupType,
  navNameItemType,
  toggleDetailsModalObjType,
} from "./types";
import { useI18n } from "vue-i18n";

// eslint-disable-next-line @typescript-eslint/ban-ts-comment
// @ts-ignore
import video from "@/assets/media/banner.mp4";

interface stateType {
  handCount: Ref<boolean>;
  skillNumTemp: Ref<number[]>;
  collectNumTemp: Ref<number[]>;
  bannerVideo: Ref<any>;
  collectUrl: Ref<string>;
  rwdStatus: Ref<boolean>;
  rwdTitleAnimate: Ref<boolean>;
  rwdMenuAnimate: Ref<boolean>;
  rwdIconAnimate: Ref<boolean>;
  rwdIconOptionsAnimate: Ref<boolean>;
  scrollTemp: Ref<number>;
  goTopAnimate: Ref<boolean>;
  arrowAnimate: Ref<boolean>;
  mainSkillOuterAnimate: Ref<boolean>;
  mainSkillAnimate: Ref<boolean>;
  collectionOuterAnimate: Ref<boolean>;
  collectionAnimate: Ref<boolean>;
  hobbyOuterAnimate: Ref<boolean>;
  hobbyAnimate: Ref<boolean>;
  selfOuterAnimate: Ref<boolean>;
  selfAnimate: Ref<boolean>;
  selfDetailsAnimate: Ref<boolean>;
  byHand: Ref<boolean>;
  toggleModalObj: Ref<toggleModalObjType>;
  goToTitleName: Ref<goToTitleNameType[]>;
  navNameItem: Ref<navNameItemType[]>;
  contentItem: Ref<skillItemType[]>;
  collection: Ref<collectionItemType[]>;
  selectLanguageVal: Ref<string>;
  languageGroup: Ref<languageGroupType[]>;
  toggleLanguageListStatus: Ref<boolean>;
  filterCollectItem: Ref<collectionItemType[]>;
  toggleNavBarStatus: Ref<boolean>;
  currentNavPos: Ref<number>;
  toggleDetailsModalObj: Ref<toggleDetailsModalObjType>;
  toggleDetailsModalStatus: Ref<boolean>;
}

interface stateMethodType {
  getState: <T>(objProps: string) => T;
  setState: <T>(objProps: string) => (newVal: T) => void;
}

interface methodType {
  scrollContentPart: () => void;
  scrollContent: (
    screenScrollY: number,
    arrowPos: number,
    skillPos: number,
    collectionPos: number,
    hobbyPos: number,
    selfPos: number
  ) => void;
  scrollItems: () => void;
  showNavBg: (num: number) => void;
  hideNavBg: () => void;
  goTop: () => void;
  goToTitle: (title: number) => void;
  showRwdMenu: (time: number) => void;
  showSkill: (num: number) => void;
  showCollectContent: (num: number) => void;
  openNewWindow: (url: string) => void;
  toggleModal: (url: string, status: boolean) => void;
  formatLanguage: (
    formatStr: string,
    replaceObj?: { [key: string]: any }
  ) => string;
  setCurrentLanguage: (lang: string) => void;
  toggleLanguageList: (status: boolean) => void;
  toggleNavBar: (status: boolean) => void;
  formatSelectlanguage: (formatVal: string) => string;
  filterCurrentCollect: (filterNum: number) => void;
  initialData: () => void;
  toggleDetailsModal: (status: boolean) => void;
}

export default defineComponent({
  components: {
    Modal,
    DetailsModal,
  },
  setup() {
    const { t, locale } = useI18n();

    const state: stateType = {
      handCount: ref<boolean>(false),
      skillNumTemp: ref<number[]>([]),
      collectNumTemp: ref<number[]>([]),
      bannerVideo: ref<any>(video),
      collectUrl: ref<string>(""),
      rwdStatus: ref<boolean>(false),
      rwdTitleAnimate: ref<boolean>(false),
      rwdMenuAnimate: ref<boolean>(false),
      rwdIconAnimate: ref<boolean>(false),
      rwdIconOptionsAnimate: ref<boolean>(false),
      scrollTemp: ref<number>(0),
      goTopAnimate: ref<boolean>(false),
      arrowAnimate: ref<boolean>(false),
      mainSkillOuterAnimate: ref<boolean>(false),
      mainSkillAnimate: ref<boolean>(false),
      collectionOuterAnimate: ref<boolean>(false),
      collectionAnimate: ref<boolean>(false),
      hobbyOuterAnimate: ref<boolean>(false),
      hobbyAnimate: ref<boolean>(false),
      selfOuterAnimate: ref<boolean>(false),
      selfAnimate: ref<boolean>(false),
      selfDetailsAnimate: ref<boolean>(false),
      byHand: ref<boolean>(false),
      toggleModalObj: ref<{
        toggleSwitch: boolean;
        url: string;
      }>({
        toggleSwitch: false,
        url: "",
      }),
      goToTitleName: ref<goToTitleNameType[]>([
        {
          titleNum: 1,
          titleName: ".self-outer",
        },
        {
          titleNum: 2,
          titleName: ".hobby-outer",
        },
        {
          titleNum: 3,
          titleName: ".main-skill-outer",
        },
        {
          titleNum: 4,
          titleName: ".collection-outer",
        },
      ]),
      navNameItem: ref<navNameItemType[]>([]),
      contentItem: ref<skillItemType[]>(skillItem),
      collection: ref<collectionItemType[]>(collectionItem),
      selectLanguageVal: ref<string>(locale.value),
      languageGroup: ref<languageGroupType[]>([]),
      toggleLanguageListStatus: ref<boolean>(false),
      filterCollectItem: ref<collectionItemType[]>([]),
      toggleNavBarStatus: ref<boolean>(false),
      currentNavPos: ref<number>(0),
      toggleDetailsModalObj: ref<toggleDetailsModalObjType>({
        useSkillContent: "1",
        useKitContent: "1",
        collectDescContent: "1",
      }),
      toggleDetailsModalStatus: ref<boolean>(false),
    };

    const { getState, setState }: stateMethodType = {
      getState(objProps) {
        return (state as { [key: string]: any })[objProps].value;
      },
      setState(objProps) {
        return (newVal) =>
          ((state as { [key: string]: any })[objProps].value = newVal);
      },
    };

    const method: methodType = {
      formatLanguage(formatStr, replaceObj) {
        return replaceObj ? t(formatStr, replaceObj!) : t(formatStr);
      },
      filterCurrentCollect(filterNum) {
        const filterItem: collectionItemType[] = $.filter(
          getState<collectionItemType[]>("collection"),
          ({ collectNum }: collectionItemType) => collectNum === filterNum
        );
        setState<collectionItemType[]>("filterCollectItem")(filterItem);
      },
      initialData() {
        setState<navNameItemType[]>("navNameItem")([
          {
            num: 1,
            name: method.formatLanguage("selfTitle"),
          },
          {
            num: 2,
            name: method.formatLanguage("hobbyTitle"),
          },
          {
            num: 3,
            name: method.formatLanguage("skillTitle"),
          },
          {
            num: 4,
            name: method.formatLanguage("collectionTitle"),
          },
        ]);

        setState<languageGroupType[]>("languageGroup")([
          {
            displayName: method.formatLanguage("zh"),
            value: "zh",
          },
          {
            displayName: method.formatLanguage("en"),
            value: "en",
          },
        ]);

        method.filterCurrentCollect(1);
      },
      formatSelectlanguage(formatVal) {
        return getState<languageGroupType[]>("languageGroup")
          .filter((item: languageGroupType) => item.value.match(formatVal))
          .map((item: languageGroupType) => item.displayName)
          .join("");
      },
      setCurrentLanguage(lang) {
        setState<string>("selectLanguageVal")(lang);
        setState<boolean>("toggleLanguageListStatus")(false);
      },
      toggleNavBar(status) {
        setState<boolean>("toggleNavBarStatus")(!status);
      },
      toggleLanguageList(status) {
        setState<boolean>("toggleLanguageListStatus")(status);
      },
      toggleModal(url, status) {
        setState<toggleModalObjType>("toggleModalObj")({
          toggleSwitch: status,
          url: url,
        });
      },
      toggleDetailsModal(status) {
        const [{ collectSkill, collectPlugin, collectDesc }] =
          getState<collectionItemType[]>("filterCollectItem");
        setState<boolean>("toggleDetailsModalStatus")(status);
        setState<toggleDetailsModalObjType>("toggleDetailsModalObj")({
          useSkillContent: method.formatLanguage("collectUseSkill", {
            text: collectSkill,
          }),
          useKitContent: method.formatLanguage("collectUseKitsAndFrames", {
            text:
              collectPlugin === "None" && locale.value === "zh"
                ? "無"
                : collectPlugin,
          }),
          collectDescContent: method.formatLanguage(collectDesc),
        });
      },
      openNewWindow(url) {
        window.open(url);
      },
      scrollContentPart() {
        const rwdEqual: number =
          getState<boolean>("rwdStatus") === true ? 4 : 2;
        const downArrow: HTMLDivElement = $(".down-arrow");
        const mainSkillOuter: HTMLDivElement = $(".main-skill-outer");
        const collectionOuter: HTMLDivElement = $(".collection-outer");
        const hobbyOuter: HTMLDivElement = $(".hobby-outer");
        const selfOuter: HTMLDivElement = $(".self-outer");
        const screenScrollY: number = window.scrollY + window.innerHeight;
        const selfPos: number = selfOuter.offsetTop;
        const arrowPos: number =
          downArrow.offsetTop + downArrow.offsetHeight / rwdEqual;
        const hobbyPos: number =
          hobbyOuter.offsetTop + hobbyOuter.offsetHeight / rwdEqual;
        const skillPos: number =
          mainSkillOuter.offsetTop + mainSkillOuter.offsetHeight / rwdEqual;
        const collectionPos: number =
          collectionOuter.offsetTop +
          collectionOuter.offsetHeight / 2 / rwdEqual;
        method.scrollContent(
          screenScrollY,
          arrowPos,
          skillPos,
          collectionPos,
          hobbyPos,
          selfPos
        );
      },
      scrollItems() {
        if (window.scrollY >= 55) {
          if (window.scrollY < getState<number>("scrollTemp")) {
            setState<boolean>("rwdTitleAnimate")(false);
            setState<boolean>("rwdIconOptionsAnimate")(false);
          } else {
            getState<boolean>("byHand") == false
              ? setState<boolean>("rwdTitleAnimate")(true)
              : setTimeout(
                  () => setState<boolean>("rwdTitleAnimate")(true),
                  3050
                );
            method.toggleNavBar(true);
            setState<boolean>("rwdIconOptionsAnimate")(
              !(getState<boolean>("rwdIconAnimate") === true)
            );
          }
          setState<number>("scrollTemp")(window.scrollY);
        }

        setState<boolean>("goTopAnimate")(window.scrollY >= 150);
      },
      scrollContent(
        screenScrollY,
        arrowPos,
        skillPos,
        collectionPos,
        hobbyPos,
        selfPos
      ) {
        method.scrollItems();
        arrowPos < screenScrollY
          ? setState<boolean>("arrowAnimate")(true)
          : setState<boolean>("arrowAnimate")(false);

        if (selfPos < screenScrollY) {
          method.showNavBg(0);
          setState<boolean>("arrowAnimate")(true);
          setState<boolean>("selfAnimate")(true);
          setTimeout(() => setState<boolean>("selfDetailsAnimate")(true), 501);
        } else {
          setState<boolean>("selfAnimate")(false);
          setState<boolean>("selfDetailsAnimate")(false);
        }

        if (hobbyPos < screenScrollY) {
          method.showNavBg(1);
          setState<boolean>("selfOuterAnimate")(true);
          setTimeout(() => setState<boolean>("hobbyAnimate")(true), 700);
        } else {
          setState<boolean>("selfOuterAnimate")(false);
          setState<boolean>("hobbyAnimate")(false);
        }

        if (skillPos < screenScrollY) {
          method.showNavBg(2);
          setState<boolean>("hobbyOuterAnimate")(true);
          setTimeout(() => setState<boolean>("mainSkillAnimate")(true), 700);
        } else {
          setState<boolean>("mainSkillAnimate")(false);
          setState<boolean>("hobbyOuterAnimate")(false);
        }

        if (collectionPos < screenScrollY) {
          method.showNavBg(3);
          setState<boolean>("mainSkillOuterAnimate")(true);
          setTimeout(() => setState<boolean>("collectionAnimate")(true), 700);
        } else {
          setState<boolean>("collectionAnimate")(false);
          setState<boolean>("mainSkillOuterAnimate")(false);
        }
      },
      showNavBg(num) {
        if (getState<boolean>("rwdStatus") === true) {
          $.each($(".nav-rwd ul li"), (item: HTMLDivElement) => {
            $.convert($(item).attr("data-list"), "number") === num
              ? $(item).addClass("list-active")
              : $(item).removeClass("list-active");
          });
          return;
        }
        const navBody: DOMRect = document
          .querySelector(".nav-option")!
          .getBoundingClientRect();
        setTimeout(() => {
          $(".nav-bg").addClass("nav-bg-active");
          $.each($(".nav-option"), (element: HTMLDivElement) => {
            const useMethod: string =
              $.convert($(element).attr("data-num"), "number") === num + 1
                ? "addClass"
                : "removeClass";
            $(element)[useMethod]("toggle-color")[useMethod]("active");
          });
        }, 100);
        $(".nav-bg")
          .styles(
            "set",
            "left",
            `${num == 0 ? navBody.left : navBody.width * num}px`
          )
          .styles("set", "width", `${navBody.width}px`)
          .styles("set", "height", `${navBody.height}px`);
      },
      hideNavBg() {
        $(".nav-bg").removeClass("nav-bg-active");
        if (getState<number>("currentNavPos") === 0) {
          $.each($(".nav-option"), (element: HTMLDivElement) =>
            $(element).removeClass("toggle-color")
          );
        }
      },
      goTop() {
        $("html").scrollToTop({ scrollTop: 0, duration: 2500 });
      },
      goToTitle(title) {
        setState<boolean>("byHand")(true);
        setTimeout(() => setState<boolean>("byHand")(false), 3050);
        setState<number>("currentNavPos")(title);
        $.each(
          getState<boolean>("goToTitleName"),
          ({ titleNum, titleName }: goToTitleNameType) => {
            if (titleNum === title) {
              if (!getState<boolean>("rwdStatus")) {
                $($(".nav-option")[titleNum - 1])
                  .addClass("toggle-color")
                  .addClass("active");
              }

              $("html").scrollToTop({
                scrollTop: $(titleName).props("offsetTop"),
                duration: 2500,
              });
            } else {
              if (!getState<boolean>("rwdStatus")) {
                $($(".nav-option")[titleNum - 1])
                  .removeClass("toggle-color")
                  .removeClass("active");
              }
            }
          }
        );

        getState<boolean>("rwdStatus") && method.showRwdMenu(2550);
      },
      showRwdMenu(time) {
        const timeTemp: number = time === 2550 ? time : 0;
        if (getState<boolean>("rwdIconAnimate") === false) {
          setState<boolean>("rwdIconAnimate")(true);
          $(".nav-rwd-outer").styles("set", "display", "block");
          setTimeout(() => setState<boolean>("rwdMenuAnimate")(true), 100);
        } else {
          setTimeout(() => {
            setState<boolean>("rwdMenuAnimate")(false);
            setState<boolean>("rwdIconAnimate")(false);
            getState<boolean>("rwdTitleAnimate") == false
              ? setState<boolean>("rwdIconOptionsAnimate")(false)
              : setState<boolean>("rwdIconOptionsAnimate")(true);
            setTimeout(
              () => $(".nav-rwd-outer").styles("remove", "display"),
              901
            );
          }, timeTemp);
        }
      },
      showSkill(num) {
        const pos = $.indexOf(getState<number[]>("skillNumTemp"), num);
        if (pos == -1) {
          const skillNumItemTemp: number[] = [
            ...getState<number[]>("skillNumTemp"),
          ];
          setState<number[]>("skillNumTemp")([...skillNumItemTemp, num]);
          $($(".skill-card-body")[num]).addClass("skill-card-body-show");
          $($(".skill-card-body-outer")[num]).addClass(
            "skill-card-body-outer-toggle"
          );
        } else {
          $.each(getState<number[]>("skillNumTemp"), (item: number) => {
            if (item == num) {
              $($(".skill-card-body")[num]).removeClass("skill-card-body-show");
              $($(".skill-card-body-outer")[num]).removeClass(
                "skill-card-body-outer-toggle"
              );
            }
          });
          const skillNumItemTemp: number[] = [
            ...getState<number[]>("skillNumTemp"),
          ];
          skillNumItemTemp.splice(pos, 1);
          setState<number[]>("skillNumTemp")(skillNumItemTemp);
        }
      },
      showCollectContent(num) {
        const pos = $.indexOf(getState<number[]>("collectNumTemp"), num);
        if (pos == -1) {
          const collectNumItemTemp: number[] = [
            ...getState<number[]>("collectNumTemp"),
          ];
          setState<number[]>("collectNumTemp")([...collectNumItemTemp, num]);
          $($(".collect-content")[num]).addClass("collect-content-show");
          $($(".arrows")[num].childNodes[0]).addClass("line-ft-animate");
          $($(".arrows")[num].childNodes[1]).addClass("line-sd-animate");
        } else {
          $.each(getState<number[]>("collectNumTemp"), (item: number) => {
            if (item == num) {
              $($(".collect-content")[num]).removeClass("collect-content-show");
              $($(".arrows")[num].childNodes[0]).removeClass("line-ft-animate");
              $($(".arrows")[num].childNodes[1]).removeClass("line-sd-animate");
            }
          });
          const collectNumItemTemp: number[] = [
            ...getState<number[]>("collectNumTemp"),
          ];
          collectNumItemTemp.splice(pos, 1);
          setState<number[]>("collectNumTemp")(collectNumItemTemp);
        }
      },
    };

    onUpdated(() => {
      if (getState<string>("selectLanguageVal") !== locale.value) {
        locale.value = getState<string>("selectLanguageVal");
        method.initialData();
      }
    });

    onBeforeMount(() => {
      method.initialData();
      $(window).listener("scroll", method.scrollContentPart);
      setState<boolean>("rwdStatus")(window.innerWidth <= 1024);
    });

    onMounted(() => {
      $("html").scrollToTop({ scrollTop: 1, duration: 2500 });
    });

    onUnmounted(() => {
      $(window).removeListener("scroll", method.scrollContentPart);
    });

    const transSelectLanguageVal = computed(() =>
      method.formatSelectlanguage(getState<string>("selectLanguageVal"))
    );

    return { ...state, ...method, t, locale, transSelectLanguageVal };
  },
});
