
import { defineComponent } from "vue";
import { toggleModalObjType } from "./types";

interface Props {
  toggleModalObjProps: toggleModalObjType;
  toggleModalProps: (url: string, status: boolean) => void;
}

interface methodType {
  useToggleModalProps: () => void;
}

export default defineComponent({
  props: ["toggleModalObjProps", "toggleModalProps"],
  setup(props: Props) {
    const method: methodType = {
      useToggleModalProps() {
        const { toggleModalObjProps, toggleModalProps }: Props = props;
        toggleModalProps(toggleModalObjProps.url, false);
      },
    };

    return { ...method };
  },
});
